.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


body { padding: 50px; 
  font-family: 'Libre Baskerville', serif;
   background: #2d2d1fbf; -webkit-font-smoothing: antialiased;
}

h1 { 
  text-align: center; 
  margin: 0px;  
  font-size: 40px; border-top: .2em solid #fff; 
  border-bottom: .3em solid #fff;  
  margin: .25em auto 1.5em; 
  padding: 20px; width: 50%; }

.story {
  position:relative;
  float: none;
  background: rgb(250, 250, 239); 
  font-size: 1em;
   display: block;
  width: 70%;
  text-align: justify;
  padding: 5%;
  -webkit-box-shadow: 510px 10px 30px 0px rgba( 0, 0, 0, 0.9);
  box-shadow:0 2rem 1.2rem rgba(0,0,0,0.7);
  min-height: 200px;
    float: none;
    clear:both;
    margin: 0px auto 40px;
    width: auto;
    padding: 10%;
  color: #111;
  text-indent: 2em;
  line-height: 1.5em;
}

.text p.page-number {
  display: block;
  position:absolute;
  width: 100%;
  text-align: center;
  bottom: 30px;
  left:0px;
  font-size: 100%;
  text-indent: 0px;
}

a { 
  color: #000; 
  border-bottom: 1px dashed #000;
  text-decoration: none;
  font-weight: bold;
  &:hover {
    border-bottom: 1px solid #000;
  }
}

div.quote {
    float: none;
    clear:both;
    margin: 0px auto 40px;
    width: auto;
    padding: 10%;
   position: relative;
  padding: 50px 50px;
   position:relative;
  font-style: italic;
}

div.quote .author {
   position: absolute;
  bottom: -2.5em;
  right: 0px;
  font-size: .8em;
  font-style: normal;
}





* {
  box-sizing: border-box;
}


.wrapper {
  width: 100%;
  padding: 0 2rem;
  text-align: center;
}
.polaroid {
  background: #fff;
  padding: 1rem;
  box-shadow: 0 0.2rem 1.2rem rgba(0,0,0,0.2);
  
}
.polaroid > img{
  max-width: 100%;
  height: auto;
}
.caption {
  font-size: 1.8rem;
  text-align: center;
  line-height: 2em;
  color: darkgrey;
}
.item {
  width: 30%;
  display: inline-block;
  margin-top: 2rem;
  filter: grayscale(100%);
}
.item .polaroid:before {
  content: '';
  position: absolute;
  z-index: -1;
  transition: all 0.35s;
}
.item:nth-of-type(4n+1) {
  transform: scale(0.8, 0.8) rotate(5deg);
  transition: all 0.35s;
}
.item:nth-of-type(4n+1) .polaroid:before {
  transform: rotate(6deg);
  height: 20%;
  width: 47%;
  bottom: 30px;
  right: 12px;
  box-shadow: 0 2.1rem 2rem rgba(0,0,0,0.4);
}
.item:nth-of-type(4n+2) {
  transform: scale(0.8, 0.8) rotate(-5deg);
  transition: all 0.35s;
}
.item:nth-of-type(4n+2) .polaroid:before {
  transform: rotate(-6deg);
  height: 20%;
  width: 47%;
  bottom: 30px;
  left: 12px;
  box-shadow: 0 2.1rem 2rem rgba(0,0,0,0.4);
}
.item:nth-of-type(4n+4) {
  transform: scale(0.8, 0.8) rotate(3deg);
  transition: all 0.35s;
}
.item:nth-of-type(4n+4) .polaroid:before {
  transform: rotate(4deg);
  height: 20%;
  width: 47%;
  bottom: 30px;
  right: 12px;
  box-shadow: 0 2.1rem 2rem rgba(0,0,0,0.3);
}
.item:nth-of-type(4n+3) {
  transform: scale(0.8, 0.8) rotate(-3deg);
  transition: all 0.35s;
}
.item:nth-of-type(4n+3) .polaroid:before {
  transform: rotate(-4deg);
  height: 20%;
  width: 47%;
  bottom: 30px;
  left: 12px;
  box-shadow: 0 2.1rem 2rem rgba(0,0,0,0.3);
}
.item:hover {
  filter: none;
  transform: scale(1, 1) rotate(0deg) !important;
  transition: all 0.35s;
}
.item:hover .polaroid:before {
  content: '';
  position: absolute;
  z-index: -1;
  transform: rotate(0deg);
  height: 90%;
  width: 90%;
  bottom: 0%;
  right: 5%;
  box-shadow: 0 1rem 3rem rgba(0,0,0,0.2);
  transition: all 0.35s;
}


.footnote:target {
  animation: yellowflash-outline 1s;
}
.footnotes :target {
  animation: yellowflash-bg 1s;
}
@keyframes yellowflash-bg {
  from { background: yellow; }
  to   { background: transparent; }
}
@keyframes yellowflash-outline {
  from { outline: 10px solid yellow; }
  to   { outline: 10px solid transparent; }
}
html {
  scroll-behavior: smooth;
}


